html {
  font-size: 100%;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

a, a:visited, a:active {
  text-decoration: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-Rubik);
  color: #fff;
  background: var(--very_dark_blue);
}

:root {
  --blue:hsl(246, 80%, 60%);
  --light_red_work:hsl(15, 100%, 70%);
  --soft_blue_play:hsl(195, 74%, 62%);
  --light_red_study:hsl(348, 100%, 68%);
  --lime_green_exercise:hsl(145, 58%, 55%);
  --violet_social:hsl(264, 64%, 52%);
  --soft_orange_selfcare:hsl(43, 84%, 65%);
  --very_dark_blue:hsl(226, 43%, 10%);
  --dark_blue:hsl(235, 46%, 20%);
  --desaturated_blue:hsl(235, 45%, 61%);
  --pale_Blue: hsl(236, 100%, 87%);
}

:root {
  --font-Rubik: "Rubik", sans-serif;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  margin: 5rem auto;
  padding: 1.4375rem;
  gap: 1.25rem;
}
@media (min-width: 71.875em) {
  .container {
    /* margin: rem(250) auto; */
    grid-template-columns: auto auto;
    grid-template-rows: auto;
    max-width: 69.375rem;
    gap: 1.875rem;
  }
}

.foo {
  --width: ;
}

.cards {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto auto auto auto;
  gap: 1.25rem;
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(3, auto);
    grid-template-rows: repeat(2, auto);
    gap: 1.875rem;
  }
}

.card {
  position: relative;
  background: var(--dark_blue);
  border-radius: 12px;
  margin-top: 2.875rem;
  width: 100%;
}
@media (min-width: 71.875em) {
  .card {
    min-width: 15.21875rem;
  }
}
.card::after, .card::before {
  content: "";
  position: absolute;
  border-radius: 12px 12px 0 0;
  width: 100%;
  height: 62px;
  top: -46px;
  z-index: -1;
}
.card:hover {
  background: #33387a;
  cursor: pointer;
}
.card--work::after, .card--work::before {
  background: url("../images/icon-work.svg");
  background-color: var(--light_red_work);
  background-repeat: no-repeat;
  background-position: top -0.3125rem right 1.3125rem;
}
.card--play::after, .card--play::before {
  background: url("../images/icon-play.svg");
  background-color: var(--soft_blue_play);
  background-repeat: no-repeat;
  background-position: top -0.3125rem right 1.3125rem;
}
.card--study::after, .card--study::before {
  background: url("../images/icon-study.svg");
  background-color: var(--light_red_study);
  background-repeat: no-repeat;
  background-position: top -0.3125rem right 1.3125rem;
}
.card--exercise::after, .card--exercise::before {
  background: url("../images/icon-exercise.svg");
  background-color: var(--lime_green_exercise);
  background-repeat: no-repeat;
  background-position: top 0.0625rem right 1.3125rem;
}
.card--social::after, .card--social::before {
  background: url("../images/icon-social.svg");
  background-color: var(--violet_social);
  background-repeat: no-repeat;
  background-position: top -1rem right 1.3125rem;
}
.card--selfcare::after, .card--selfcare::before {
  background: url("../images/icon-self-care.svg");
  background-color: var(--soft_orange_selfcare);
  background-repeat: no-repeat;
  background-position: top -0.6875rem right 1.3125rem;
}
.card__title {
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 1;
}
.card__time {
  font-size: 1.875rem;
  font-weight: 300;
  line-height: 1;
}
@media (min-width: 71.875em) {
  .card__time {
    font-size: 2.8125rem;
  }
}
.card__previous {
  font-size: 0.875rem;
  color: var(--pale_Blue);
  font-weight: 400;
  line-height: 1;
}

.header {
  width: fit-content;
  font-weight: 300;
  width: 100%;
}
.header__info {
  display: flex;
  position: relative;
  align-items: center;
  background: var(--blue);
  border-radius: 0.75rem 0.75rem 0 0;
  text-align: left;
  padding: 1.875rem 1.875rem 0.8125rem 1.875rem;
  gap: 1.0625rem;
}
.header__info br {
  display: none;
}
@media (min-width: 71.875em) {
  .header__info {
    flex-direction: column;
    padding: 2.1875rem 5.25rem 3.9375rem 2rem;
    align-items: start;
  }
  .header__info br {
    display: inline;
  }
}
.header__profile_pic {
  width: 5rem;
  height: 5rem;
  border: 0.1875rem #fff solid;
  border-radius: 22.5rem;
}
@media (min-width: 71.875em) {
  .header__profile_pic {
    margin-bottom: 2.5625rem;
  }
}
.header__name {
  font-size: 1.25rem;
  letter-spacing: 0.0625rem;
}
@media (min-width: 71.875em) {
  .header__name {
    font-size: 2.125rem;
  }
}
.header__surtitle {
  font-size: 0.75rem;
  color: var(--pale_Blue);
  font-weight: 400;
  margin-bottom: 0.3125rem;
}
.header__link {
  position: relative;
  display: flex;
  background: var(--dark_blue);
  width: auto;
  padding: 2.75rem 2.75rem 1.6875rem 1.875rem;
  border-radius: 0 0 0.75rem 0.75rem;
  justify-content: space-between;
  align-content: center;
}
.header__link::after, .header__link::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 1.0625rem;
  background: var(--blue);
  border-radius: 0 0 0.75rem 0.75rem;
}
.header__link a {
  font-weight: 400;
  color: var(--desaturated_blue);
}
.header__link a:hover {
  color: #fff;
}
@media (min-width: 71.875em) {
  .header__link {
    flex-direction: column;
    gap: 1.75rem;
    padding: 2.75rem 0 1.6875rem 1.875rem;
  }
}

.card-grid {
  display: grid;
  padding: 1.875rem;
  gap: 1rem 4.4375rem;
  justify-items: left;
  align-items: center;
}
@media (min-width: 71.875em) {
  .card-grid {
    grid-template-columns: auto 1fr;
    grid-template-rows: repeat(3, auto);
    gap: 1.375rem 1.9375rem;
  }
}
.card-grid .card__ellipsis {
  grid-column: 2/3;
  grid-row: 1/2;
  justify-self: right;
}
@media (min-width: 71.875em) {
  .card-grid .card__ellipsis {
    margin-bottom: 1.09375rem;
  }
}
.card-grid .card__previous {
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: right;
}
@media (min-width: 71.875em) {
  .card-grid .card__previous {
    grid-column: 1/3;
    grid-row: 3/4;
    justify-self: left;
  }
}
.card-grid .card__time {
  grid-column: 1/2;
  grid-row: 2/3;
}
@media (min-width: 71.875em) {
  .card-grid .card__time {
    grid-column: 1/3;
    grid-row: 2/3;
  }
}
.card-grid .card__title {
  grid-column: 1/2;
  grid-row: 1/2;
}
@media (min-width: 71.875em) {
  .card-grid .card__title {
    margin-bottom: 1.25rem;
  }
}